.card {
	width: 729px;
	height: 657px;

	display: flex;
	flex-direction: column;
	justify-content: center;
	align-content: center;
	align-items: center;

	background: rgba(58, 58, 58, 0.7);
	backdrop-filter: blur(17px);

	border-radius: 14px;
}

input:-webkit-autofill,
input:-webkit-autofill:focus,
input:-webkit-autofill:hover,
input:-webkit-autofill:active {
  -webkit-transition-delay: 9999s;
  transition-delay: 9999s;
  font-size: 24px !important; 
}

.imgLogo {
	width: 258px;
	height: 158px;
}

.textfieldStyle {
	width: 636px;
	height: 81px;

	border-radius: 8px;
	background: rgba(255, 255, 255, 0.1);
	backdrop-filter: blur(50px);
}

.actionContainer {
	height: 60%;
	width: 636px;

	display: flex;
	flex-direction: column;
	justify-content: center;

	margin-top: 10px;
}

.downContainer {
	width: 636px;

	display: flex;
	justify-content: space-between;

	margin-top: 7%;
}

.downAction {
	width: 280px;
	height: 72px;

	display: flex;
	justify-content: center;
	align-items: center;

	font-family: 'Helvetica';
	font-style: normal;
	font-weight: 700;
	font-size: 22px;
	line-height: 28px;

	color: #ffffff;
	background: #f07c00;
	border-radius: 8px;

	border: none;

	cursor: pointer;
}
.downActionHelp {
	height: 72px;

	display: flex;
	justify-content: right;
	align-items: center;

	font-family: 'Helvetica';
	font-style: normal;
	font-weight: 700;
	font-size: 20px;
	line-height: 28px;

	text-align: right;
	color: #838383;

	cursor: pointer;
	user-select: none;
}

@media (max-width: 1280px) {
	.card {
		width: 429px;
		height: 480px;

		padding-top: 2%;
	}
	.actionContainer {
		width: 336px;
	}
	.textfieldStyle {
		width: 336px;
	}
	.downContainer {
		width: 336px;
	}
	.downAction {
		width: 160px;
	}
}
@media (max-width: 1080px) {
	.card {
		height: 410px;

		padding-top: 2%;
	}
	.textfieldStyle {
		height: 50px;
	}
	.downContainer {
		width: 336px;
	}
	.downAction {
		height: 50px;

		font-size: 18px;
	}
	.downActionHelp {
		width: 160px;
		height: 50px;

		font-size: 18px;
	}
}
